@font-face {
  font-family: 'Genshin';
  src: url('Genshin_Impact.ttf');
}

body {
  margin: 0;
  font-family: 'Genshin', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body{
  background-color: #000000;
  background-image: url('night_sky.jpg');
  background-repeat: no-repeat;
}

.view{
  height: 100%;
  max-width: 100%;
  overflow-x: hidden;
  display: flex;
  flex-wrap: wrap;
}

.center{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.card{
  width: 95%;
  background: #b5e916;
  border-radius: 10px;
  padding: 4.4vw 4.4vw;
  box-sizing: border-box;
  z-index: 90;
}

.count-down-text{
  margin-bottom: 3vw;
  font-size: min(4.4vw, 4.4vh);
}

.count-down{
  font-size: min(10vw, 10vh);
  margin: 0;
}

.margin{
  transform: translateY(calc(50vh - 50%));
}

.text-card{
  min-height: 100vh;
  background: #ffffff;
  width: 100vw;
  position: absolute;
  left: 100vw;
  z-index: 100;

  padding: 20px;
  box-sizing: border-box;

  transition: left 2s linear;
}

.show{
  left: 0;
}